}

.page-module .pages .page-item button{
    color: #222;
    cursor: pointer;
    outline: none;
    text-align: center;
    border-radius: 4px;
    background-color: #fff;
    border: 1px solid #ddd;
    background-image: none;
    -webkit-transition: all .2s;
    transition: all .2s;
    font-size: 14px;
    min-width: 15px;
    margin: 0 2px;
    padding: 0 13px;
    float: left;
    display: block;
    height: 38px;
    line-height: 38px;
}

.page-module .pages .page-item.active button{
    background: #00a1d6;
    color: #fff;
    border: 1px solid #00a1d6;
}

.page-module .pages .page-item:hover button{
    background: #00a1d6;
    color: #fff;
    border: 1px solid #00a1d6;
}

.page-module .pages strong{
    display: inline-block;
    border: 1px solid transparent;
    border-radius: 4px;
