.groom-moudle{
    width: 160px;
    height: 100px;
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    background: #fff;
    float: left;
    margin: 0 0 20px 20px;
}

.a{
    -webkit-transition: color .2s;
    transition: color .2s;
}

.groom-moudle .pic{
    width: 160px;
    height: 100px;
    border: none;
    vertical-align: middle;
}

.groom-moudle .card-mark{
    position: absolute;
    left: 0;
    top: 68px;
    width: 160px;
    height: 100%;
    font-size: 12px;
    height: 30px;
    line-height: 20px;
    padding: 10px 5px;
    overflow: hidden;
    background: #000;
    background: -webkit-linear-gradient(transparent,
    rgba(0,0,0,.1) 20%,rgba(0,0,0,.2) 35%,
    rgba(0,0,0,.6) 65%,rgba(0,0,0,.9));
    background: linear-gradient(transparent,
    rgba(0,0,0,.1) 20%,rgba(0,0,0,.2) 35%,
    rgba(0,0,0,.6) 65%,rgba(0,0,0,.9));
}

.groom-moudle .card-mark .title{
    color: #fff;
    height: 40px;
    overflow: hidden;
    margin-bottom: 5px;
    word-break: break-all;
    word-wrap: break-word;
}

.groom-moudle .card-mark .up{
    opacity: 0;
    color: #99a2aa;
    height: 20px;
    line-height: 20px;
    overflow: hidden;
    -webkit-transition: all .6s;
    transition: all .6s;
}

.groom-moudle .card-mark .play{
    opacity: 0;
    color: #99a2aa;
    height: 20px;
    line-height: 20px;
    overflow: hidden;
    -webkit-transition: all .6s;
    transition: all .6s;
}

.groom-moudle:hover .card-mark{
    top: 0;
    height: 100%;
    background-color: black;
    background: -webkit-linear-gradient(transparent,
    rgba(0,0,0,.1) 20%,
    rgba(0,0,0,.2) 35%,
    rgba(0,0,0,.6) 65%,
    rgba(0,0,0,.9));
    background: linear-gradient(transparent,
    rgba(0,0,0,.1) 20%,
    rgba(0,0,0,.2) 35%,
    rgba(0,0,0,.6) 65%,
    rgba(0,0,0,.9));
}

.groom-moudle:hover .card-mark .up{
    opacity: 1;
}
.groom-moudle:hover .card-mark .play{
    opacity: 1;
}

